.card {
    position: relative;
    transition: 0.9s;
    transform-style: preserve-3d;
  }
  
  .card .front,
  .card .back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    font-size: 3rem;
    backface-visibility: hidden;
    border: 1px solid rgb(231, 135, 26);
  
    /* box shadow */
    box-shadow: 0px 6px 5px 0px #864601;
    -webkit-box-shadow: 0px 6px 5px 0px #864601;
    -moz-box-shadow: 0px 6px 5px 0px #864601;
  }
  
  .card .front {
    background: rgb(231, 135, 26);
    color: #864601;
    z-index: 2;
    transform: rotateY(0deg);
  }
  
  .card .back {
    background: rgb(241, 178, 3);
  }
  
  .card[data-flipStatus="FLIPPED"],
  .card[data-flipStatus="MATCHED"],
  .back {
    transform: rotateY(180deg);
  }
  
  .card[data-flipStatus="MATCHED"] .back {
    opacity: 0.2;
    border: 3px dashed red;
  }